
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {Modules,request} from '../utils/HttpRequest/request'
var JsBarcode = require('jsbarcode')
import { postData } from '../utils/hexiao'
import { session } from './../utils/storage'
import { couponStatus, couponType, couponUsage, couponTypeFilter } from './../config/constant'
import couponDetailMixin from './../mixins/couponDetailMixin'
import myInfoMixin from './../mixins/myInfoMixin'
import sessionMenagement from '../utils/StorageManagement'
import { Brands } from './../config/variables'
import report from './../utils/statistics'
import commonApis from './../store/apis/common'

export default {
components: {
XHeader,
XButton,
Cell
},
data() {
return {
couponType,
couponStatus,
couponUsage,
showExchange: true, // 仅限红包券使用： 是否展示 '马上使用' 按钮
status: 0,

verification:1,
coupon_type:1,
brandName: '',
coupon: '',
barCode: '',
validityDate: '',
startTime: '',
endTime: '',
desc: '',
condition: '指定商品',
dataDetail:{},
source:1,
member_id:'',
phone: '',
coupon_code:'',
brandIcon: '',
goods_url: [],
goLink:'',
jump_url:'',

company_id:2,
goods_pic: ''
}
},
mixins: [ couponDetailMixin, myInfoMixin ],
methods: {
isShowMainButton () {
const { couponDetail } = this
if (couponDetail.status != couponStatus.UNUSED) {
return false
}
if (couponDetail.type == couponType.DEDUCTION &&
couponDetail.usage == couponUsage.OFFLINE_POS) {
return false
}
return true
},

createMember (barCode) {
document.getElementById('barcode')
&& JsBarcode("#barcode", barCode, {
width: 1.8,
height: 70
})
},

dealingGoodsCoupon () {
console.log('点击了商品券线上兑换');
this.$router.push({path: '/goodsExchange', query: {}});
postData(this.dataDetail)
},

dealingDeductionCoupon () {
let { usage, url, brand } = this.couponDetail
const { phone } = this.myInfo
if (usage === couponUsage.ONLINE_MINI) {
this.$router.push({path: '/voucherExchange', query: {dataDetail :this.couponDetail}});
} else if(usage === couponUsage.THIRD_PARTY_URL) {
if (!phone) {
this.$router.push({path: '/bindMobile', query:{type :3}})
return
}
if (brand && brand.name === Brands.WU_LIANG_YE) {
commonApis.encryptPhone(phone).then(res => {
if (res.code === 200) {
url += `&param=${res.data.cipher}`
}
console.log('JUMP URL: ', url)
window.location.href = url
})
} else {
window.location.href = url
}
};
},

dealingPieceCoupon () {
const { url, brand } = this.couponDetail
if (brand.name === '和气生财') {
window.location.href = url;
} else {
this.$router.push('/ewm?brand=1')
}
},

dealingBrandCoupon () {
window.location.href = this.couponDetail.url;
},

dealingRedPacketCoupon () {
if (!this.myInfo.phone) {
this.$router.push({path: '/bindMobile', query: {type :1} })
} else {
this.showExchange = false;
}
},
exchange () {
const { type } = this.couponDetail
if (type === couponType.DEDUCTION || type === couponType.DEDUCTION_LIMITED) {
this.dealingDeductionCoupon() 
} else if (type === couponType.PIECE) {
this.dealingPieceCoupon() 
} else if (type === couponType.BRAND) {
this.dealingBrandCoupon() 
} else if (type === couponType.RED_PACKET) {
this.dealingRedPacketCoupon() 
} else if (type === couponType.GOODS) {
this.dealingGoodsCoupon() 
}
console.log('this.verification',this.verification);
report('券详情使用按钮', '点击', `${couponTypeFilter(this.couponDetail.type)}-${this.couponDetail.name}`)
postData(this.couponDetail)
},
goExchange() {
console.log('兑换地点');
},
getRedPacket() {
console.log('红包券领取说明');
}
},
mounted (){
this.dataDetail  = this.couponDetail
this.source = this.$route.query.source
this.createMember(this.couponDetail.code)
this.member_id = sessionMenagement.getMemberId()
this.coupon_code = localStorage.getItem('coupon_code')
if (this.source == 0) {
this.exchange()
}
}
}
